import React from 'react';

const FormInput = ({ hook, label, handleChange, placeholder, name, maxLength, value, type }) => {
    return (
        <React.Fragment>
            <label className="oi-form__label" htmlFor={hook}> {label} </label>
            <input
                id={hook}
                name={name}
                onChange={handleChange}
                placeholder={placeholder}
                className="oi-form__input"
                maxLength={maxLength}
                value={value}
                type={type}
        
            />
        </React.Fragment>
    );
};

export default FormInput;
