import axios from 'axios';
import React, { useEffect } from 'react';
import { getUssdBanksUrl } from '../../helpers/endpoints';
import CheckoutNav from '../checkout-nav/checkout-nav';

const USSDChannel = () => {
    useEffect(() => {
        getUssdBanks();
    });

    const getUssdBanks = async () => {
        try {
            const response = await axios.get(getUssdBanksUrl(), {
                headers: { 'Content-Type': 'application/json' }
            });

            console.log(response.data);
        } catch (err) {}
    };

    return (
        <React.Fragment>
            <div className="checkout__nav">
                <CheckoutNav />
            </div>

            {/* 
               1. Call the ussd bank endpoint ->
               2. Populate the dropdown fields ->
               3. After a user select a bank ->
               4. Call the ussd payment endpoint to generate the USSD for payment ->
               5. If it fails return an ErrorModal ->
               6. If it's successful return a SuccessModal ->
             */}

            <form className="oi-form">
                <div className="">Choose your bank to start the payment</div>
            </form>
        </React.Fragment>
    );
};

export default USSDChannel;
