import React, { useContext, useEffect } from 'react';
import { withRouter } from 'react-router-dom';

import { ReactComponent as SuccessIMG } from '../../Assets/images/svg/successful-img.svg';
import { GlobalContext } from '../../context/globalContext';
import { destroy } from '../../helpers/destroy';

import { currencyFormatter } from '../../utils/TextTransform';

const SuccessMessage = ({ callback }) => {
    const {
        receiver,
        merchantCustomer: { amount },
        successResponse,
        setSpayCheckoutModal,
        spayCheckoutModal
    } = useContext(GlobalContext);

    useEffect(() => {
        const runSuccess = window.setTimeout(() => {
            // check if the call back is defined before running it
            if (typeof callback === 'function') callback(successResponse);
        }, 5000);
        return () => {
            window.clearTimeout(runSuccess);
        };
    }, []);

    return (
        <>
            {spayCheckoutModal && (
                <div className="oi-modal--mod oi-modal">
                    <div className="oi-modal__container">
                        <div className="oi-modal__card--mod oi-modal__card oi-modal__cardwd--437">
                            <div className="checkout">
                                <div className="checkout__content">
                                    <div className="checkout__successful-img">
                                        <SuccessIMG />
                                    </div>

                                    <div className="checkout__successful-msg">
                                        <div className="successful-msg__header">
                                            Transaction Sucessful
                                        </div>

                                        <div className="successful-msg__message">
                                            Your payment of
                                            <span>
                                                {' ' +
                                                    currencyFormatter.format(
                                                        amount
                                                    )}
                                            </span>{' '}
                                            to
                                            <span> {receiver}</span> was
                                            successful. Thank You!
                                        </div>
                                    </div>

                                    <button
                                        className="oi-btn"
                                        onClick={() => {
                                            // check if the call back is defined before running it
                                            if (typeof callback === 'function')
                                                callback(successResponse);
                                            setTimeout(() => {
                                                setSpayCheckoutModal(false);
                                                setTimeout(() => {
                                                    destroy();
                                                }, 500);
                                            }, 1500);
                                        }}
                                    >
                                        Done
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

export default withRouter(SuccessMessage);
