import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';

import { ReactComponent as CardIcon } from '../../Assets/images/svg/cardIcon.svg';
import { ReactComponent as BankIcon } from '../../Assets/images/svg/bankIcon.svg';
import { ReactComponent as UssdIcon } from '../../Assets/images/svg/ussdIcon.svg';
import { ReactComponent as QRIcon } from '../../Assets/images/svg/qrIcon.svg';

const CheckoutNav = () => {
    const history = useHistory();
    const [state] = useState({
        checkoutNav: [
            {
                name: 'Card',
                url: '/',
                icon: <CardIcon className="oi-icon__card" />
            },
            {
                name: 'Bank',
                url: '/bank',
                icon: <BankIcon className="oi-icon__bank" />
            },
            {
                name: 'USSD',
                url: '/ussd',
                icon: <UssdIcon className="oi-icon__ussd" />,
                style: {
                    width: '1.91rem',
                    height: '2.21rem',
                    marginRight: '.1rem'
                }
            },
            {
                name: 'Visa QR',
                url: '/visaqr',
                icon: <QRIcon className="oi-icon__visa" />
            }
        ]
    });

    return (
        <ul className="checkout__nav-list">
            {state.checkoutNav.map(list => (
                <Link
                    to={list.url !== '/visaqr' ? list.url : '/'}
                    key={list.name}
                    className={`${
                        list.url === history.location.pathname ? 'actively-selected' : ''
                    } checkout__nav-items`}>
                    <div className="checkout__nav-link">
                        <span>{list.icon}</span>
                        <span>{list.name}</span>
                    </div>
                </Link>
            ))}
        </ul>
    );
};

export default CheckoutNav;
