import axios from 'axios';
import React, { useState, useEffect, useContext } from 'react';
import PinInput from 'react-pin-input';
import { GlobalContext } from '../../context/globalContext';
import { paymentUrl } from '../../helpers/endpoints';
import { decryptDes, encryptDes } from '../../helpers/utils';
import { getPaymentStatus } from '../../utils/TextTransform';

const inputStyle = {
    // Box Model
    width: '4.5rem',
    height: '4.5rem',
    margin: '0.8rem .4rem 1rem .4rem',
    paddingLeft: '1.5rem',
    paddingRight: '1.5rem',

    // Typography
    fontFamily: "'Poppins', sans-serif",

    // Visual
    color: '#000000',
    backgroundColor: '#ffffff',
    border: '1px solid #d5d5d5',
    borderRadius: '0.5rem'
};

const CheckoutPin = props => {
    const { merchantCustomer, cardDetails, setProcessLoading, setError, setSuccessResponse } = useContext(
        GlobalContext
    );

    const [state, setstate] = useState({
        pin: ''
    });
    useEffect(() => {
        if (state.pin.length === 4) {
            handleSubmit();
            setProcessLoading(true);
        }
    }, [state]);

    const onCancel = () => {
        props.history.push('/');
    };

    const handleSubmit = async () => {
        try {
            // const { REACT_APP_encKey } = process.env;
            const { pin } = state;

            const credentials = { card: { pin, ...cardDetails } };
            const paymentMethod = encryptDes(credentials, merchantCustomer['merchantCode']);
            const values = { ...merchantCustomer, paymentMethod };

            const { data } = await axios.post(
                paymentUrl(),
                { ...values },
                {
                    headers: { 'Content-Type': 'application/json' }
                }
            );

            // TODO: WORKING HERE
            // set success response for the user callback to be used
            setSuccessResponse(data);
            // Before getting to success page check the status and if the status is otp validation take to otp page
            const destination = getPaymentStatus(data.status);
            props.history.push(destination);
            setProcessLoading(false);
        } catch (error) {
            // console.log('Error occurs here');
            const errorValue =
                error.response.data.error.message || 'An error occurred while performing this transaction';

            setError(errorValue);
            setProcessLoading(false);
        }
    };

    return (
        <form className="oi-form" onSubmit={handleSubmit}>
            <header className="pin-instruction">
                <div className="pin-instruction__message">
                    Please enter your 4-digit card pin to authorize this payment
                </div>
            </header>

            <div className="oi-form__group u-box-justify-items">
                <div className="pin-container">
                    <PinInput
                        length={4}
                        focus
                        secret
                        type="numeric"
                        onChange={(value, index) => {
                            setstate({ ...state, pin: value });
                        }}
                        inputStyle={inputStyle}
                        autoSelect={true}
                    />
                </div>
            </div>

            <button className="oi-form__cancel-btn" type="button" onClick={onCancel}>
                Cancel
            </button>
        </form>
    );
};

export default CheckoutPin;
