import React, { createContext, useState } from 'react';
import { generateRef } from '../helpers/utils';

const GlobalContext = createContext();
const { Provider } = GlobalContext;

const GlobalDetailsProvider = ({ children }) => {
    const [processLoading, setProcessLoading] = useState(false);
    const [error, setError] = useState('');
    const [successResponse, setSuccessResponse] = useState({});
    const [cardDetails, setCardDetails] = useState({
        pan: '',
        expiry: '',
        cvv: '',
        issuer: ''
    });
    const [spayCheckoutModal, setSpayCheckoutModal] = useState(true);

    const [receiver, setReceiver] = useState('');
    const [cancelled, setCancelled] = useState({ title: '', message: '' });

    const [merchantCustomer, setMerchantCustomer] = useState({
        amount: '',
        currency: '',
        reference: '',
        channel: window.navigator.userAgent,
        merchantCode: '',
        customer: {
            email: '',
            lastName: '',
            firstName: '',
            phone: ''
        },
        deviceInformation: {}
    });

    const setMerchantDetails = ({
        amount,
        currency,
        reference,
        merchantCode,
        receiver,
        deviceInformation,
        customer: { lastName, firstName, phone, email }
    }) => {
        // TODO: Change CARD to windows.navigator.platform
        setMerchantCustomer({
            amount,
            channel: window.navigator.userAgent,
            currency: currency || 'NGN',
            reference: reference === undefined || '' ? generateRef() : reference,
            merchantCode,
            customer: {
                email,
                lastName: lastName || '',
                firstName: firstName || '',
                phone: phone || ''
            },
            deviceInformation
        });
        // set callback

        setReceiver(receiver);
    };

    const setCardInfo = ({ pan, expiry, cvv, issuer }) => {
        setCardDetails({ pan, expiry, cvv, issuer });
    };

    return (
        <Provider
            value={{
                setCardDetails: cardInfo => setCardInfo(cardInfo),
                cardDetails,
                setMerchantCustomer: data => setMerchantDetails(data),
                merchantCustomer,
                receiver,
                error,
                setError: message => setError(message),
                processLoading,
                setProcessLoading: value => setProcessLoading(value),
                spayCheckoutModal,
                setSpayCheckoutModal: value => setSpayCheckoutModal(value),
                cancelled,
                setCancelled: value => setCancelled({ ...value }),
                successResponse,
                setSuccessResponse: value => setSuccessResponse(value)
            }}>
            {children}
        </Provider>
    );
};
export { GlobalDetailsProvider, GlobalContext };
