import React from 'react';

import { ReactComponent as ErrorIcon } from '../Assets/images/svg/error-icon.svg';

const CancelledModal = ({ title, message }) => {
    // This displays when the user clicks on close button or merchant didnt implement checkout correctly
    return (
        <React.Fragment>
            <div className={`${title ? 'oi-error--mod' : ''} oi-error`}>
                <div
                    className={`${
                        title ? 'oi-error__container--mod ' : ''
                    } oi-error__container`}
                >
                    <div className="oi-error__icon-wrapper">
                        <ErrorIcon />
                    </div>

                    <div className="oi-error__msg">
                        <div className="oi-error__msg-header">{title}</div>

                        <div className="oi-error__msg-text">{message}</div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

export default CancelledModal;
