import React, { createContext } from 'react';
import axios from 'axios';

// import create context from react
const FetchContext = createContext();
// get the provider of the fetch context as an instance of create context
const { Provider } = FetchContext;

const FetchProvider = ({ children }) => {
    // setting up a method to wrap a child so as to pass the value of the fetch provider down all the children within the react component

    // Auth context was setup for authenticated user to make request to anu endpoint
    // const authContext = useContext(AuthContext);
    const authAxios = axios.create({
        baseURL: process.env.REACT_APP_BASE_API
    });

    authAxios.interceptors.request.use(
        config => {
            // config.headers.Authorization = `Bearer ${authContext.authState.access_token}`;
            config.headers['Content-Type'] = 'application/json';
            return config;
        },
        error => {
            return Promise.reject(error);
        }
    );

    return (
        <Provider
            value={{
                authAxios
            }}
        >
            {children}
        </Provider>
    );
};

export { FetchContext, FetchProvider };
