import React, { useContext, useEffect, useState, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { encryptDes } from '../../helpers/utils';
import { GlobalContext } from '../../context/globalContext';
import { cardinalResponse, paymentUrl, validateVisa } from '../../helpers/endpoints';
import { getPaymentStatus } from '../../utils/TextTransform';
import axios from 'axios';
import ProcessLoader from '../process-loader/process-loader';
import ErrorModal from '../error-message/ErrorModal';
import { destroy } from '../../helpers/destroy';

const CheckoutVisa = () => {
    const { merchantCustomer, cardDetails, setSuccessResponse } = useContext(GlobalContext);

    const [cardinal, setCardinal] = useState({});
    const [cardinalResponded, setCardinalResponded] = useState(null);
    let [pendingCount, setPendingCount] = useState(0);
    const [, setError] = useState('');
    const [transactionError, setTransactionError] = useState('');

    const form = useRef();
    const history = useHistory();

    let schedulerID;

    useEffect(() => {
        if (pendingCount <= 1) {
            handleSubmit();
        }
    }, []);

    useEffect(() => {
        if (cardinalResponded !== 'COMPLETED') {
            schedulerID = setInterval(checkCardinal, 1000);
        }

        if (cardinalResponded === 'PENDING') {
            validateTransaction();
        }

        return () => clearInterval(schedulerID);
    }, [pendingCount]);

    const handleSubmit = async () => {
        const deviceInformation = {
            httpBrowserLanguage: window.navigator.language,
            httpBrowserJavaEnabled: window.navigator.javaEnabled(),
            httpBrowserColorDepth: window.screen.colorDepth,
            httpBrowserJavaScriptEnabled: true,
            httpBrowserScreenHeight: window.screen.height,
            httpBrowserScreenWidth: window.screen.width,
            httpBrowserTimeDifference: '',
            userAgentBrowserValue: window.navigator.userAgent
        };

        try {
            const credentials = { card: { pin: '', ...cardDetails } };
            const paymentMethod = encryptDes(credentials, merchantCustomer['merchantCode']);
            const datas = { ...merchantCustomer, paymentMethod, deviceInformation: deviceInformation };

            const { data } = await axios.post(
                paymentUrl(),
                { ...datas },
                {
                    headers: { 'Content-Type': 'application/json' }
                }
            );

            setCardinal(data['meta']);

            form.current.submit();
        } catch (err) {
            setError('Transaction Failed!');
        }
    };

    const checkCardinal = async () => {
        try {
            const { data } = await axios.get(cardinalResponse(merchantCustomer.reference));

            setCardinalResponded(data['hasResponse']);
            setPendingCount(pendingCount++);
        } catch (err) {
            setError("Can't fetch cardinal status ");
        }
    };

    const validateTransaction = async () => {
        try {
            const response = await axios.post(
                validateVisa(merchantCustomer.reference),
                { otp: '' },
                {
                    headers: { 'Content-Type': 'application/json' }
                }
            );

            setSuccessResponse(response.data);

            const destination = getPaymentStatus(response.data.status);
            history.push(destination);
        } catch (err) {
            const { message } = err.response.data.error;
            setTransactionError(message);
        }
    };

    const { ACSUrl, MD, jwt: JWT } = cardinal;

    if (transactionError !== '') {
        return <ErrorModal error={transactionError} closeError={() => destroy()} />;
    }

    return (
        <div>
            {(!!ACSUrl === false || cardinalResponded === 'PENDING') && <ProcessLoader />}

            <form id="form1" ref={form} action={ACSUrl} method="POST" style={{ display: 'none' }} target={'cardiFrame'}>
                <input type="text" name="JWT" value={JWT} placeholder="JWT" />
                <input type="text" name="MD" value={MD} />
                <input type="submit" value="Cardinal" />
            </form>

            <iframe id="formIframe" name="cardiFrame" title="cardinal" loading="lazy" />
        </div>
    );
};

export default CheckoutVisa;
