import axios from 'axios';
import React, { useContext, useState } from 'react';
import { GlobalContext } from '../../context/globalContext';
import { validateOtpUrl } from '../../helpers/endpoints';
import { errorMessage, getPaymentStatus } from '../../utils/TextTransform';

import FormInput from '../form-input/form-input';

const CheckoutOTP = props => {
    // const { onSubmit, onChange, onCancel } = props;
    const { successResponse, setSuccessResponse, setProcessLoading, setError } = useContext(GlobalContext);
    const [state, setState] = useState({ otp: '' });

    const handleSubmit = async event => {
        event.preventDefault();

        if (state.otp.length < 4) return;

        setProcessLoading(true);

        try {
            const { otp } = state;

            const { data } = await axios.post(
                validateOtpUrl(successResponse.reference),
                { otp },
                {
                    headers: { 'Content-Type': 'application/json' }
                }
            );

            // set success response for the user callback to be used
            setSuccessResponse(data);
            // Before getting to success page check the status and if the status is otp validation take to otp page
            const destination = getPaymentStatus(data.status);
            props.history.push(destination);

            setProcessLoading(false);
        } catch (error) {
            const errorResponse = error.response.data.error;
            const errorValue =
                errorResponse.message + errorMessage(errorResponse.code) ||
                'An error occurred while performing this transaction';
            setError(errorValue);

            setProcessLoading(false);
        }
    };

    const handleCancel = () => {
        props.history.push('/');
    };
    const handleChange = ({ target }) => {
        const { value } = target;
        // console.log(value, 'hello here');
        setState({ ...state, otp: value });
    };

    const { otp } = state;

    return (
        <form className="oi-form" onSubmit={handleSubmit}>
            <header className="otp-instruction">
                Kindly enter the OTP sent to your mobile number and your email address.
            </header>
            <div className="oi-form__group">
                <div className="otp-container">
                    <div className="oi-form__group-60-of-100">
                        <FormInput name="otp" type="number" label="otp code" value={otp} handleChange={handleChange} />
                    </div>

                    <div className="otp-button">
                        <button className="otp-authorize hover" type="submit">
                            Authorize
                        </button>
                    </div>
                </div>
            </div>
            <div className="otp-token-message">A token should be sent to you within 5 minutes.</div>
            <div className="code-resend">
                <div className="">"Didn't receive the code?"</div>

                <div className="otp-request u-half-gradient-bg">Request again</div>
            </div>
            <button className="oi-form__cancel-btn" type="button" onClick={handleCancel}>
                Cancel
            </button>
        </form>
    );
};

export default CheckoutOTP;
