import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Home from './pages/Home';
import BankChannel from './Components/bank-channel/bank-channel';
import CardChannel from './Components/card-channel/CardChannel';
import CheckoutPin from './Components/checkout-pin/CheckoutPin';
import USSDChannel from './Components/ussd-channel/ussd-channel';
import VisaQRChannel from './Components/visaqr-channel/visaqr-channel';
import CheckoutOTP from './Components/checkout-otp/checkout-otp';
import CheckoutVisa from './Components/checkout_visa/CheckoutVisa';

function HomeRoutes(props) {
    return (
        <Home {...props}>
            <Switch>
                <Route path="/bank" render={prop => <BankChannel {...prop} />} />

                <Route path="/cardchannel/pin" render={props => <CheckoutPin {...props} />} />

                <Route path="/cardchannel/visa" render={props => <CheckoutVisa {...props} />} />

                {/*<Route path="/cardchannel/visa/cardinal-response" render={props => <CardinalResponse {...props} />} />*/}

                <Route path="/ussd" render={props => <USSDChannel {...props} />} />

                <Route path="/visaqr" render={props => <VisaQRChannel {...props} />} />

                <Route path="/otp" render={props => <CheckoutOTP {...props} />} />

                <Route path="/" render={prop => <CardChannel {...prop} others={props} />} />
            </Switch>
        </Home>
    );
}

export default HomeRoutes;
