import React from 'react';

import { ReactComponent as ErrorIcon } from '../../Assets/images/svg/error-icon.svg';

const ErrorModal = ({ error, closeError }) => {
    return (
        <React.Fragment>
            <div className={`${error ? 'oi-error--mod' : ''} oi-error`}>
                <div
                    className={`${
                        error ? 'oi-error__container--mod ' : ''
                    } oi-error__container`}
                >
                    <div className="oi-error__icon-wrapper">
                        <ErrorIcon />
                    </div>

                    <div className="oi-error__msg">
                        <div className="oi-error__msg-header">Oops Error!</div>

                        <div className="oi-error__msg-text">{error}</div>
                    </div>

                    <button
                        onClick={closeError}
                        className="oi-error__close-btn"
                        type="button"
                    >
                        Close
                    </button>
                </div>
            </div>
            {/* <button onClick={this.showModal} type="button">Open</button> */}
        </React.Fragment>
    );
};

export default ErrorModal;
