export function Capitalize(data) {
    return data
        .split(' ')
        .map(item => item[0].toUpperCase() + item.slice(1))
        .join(' ');
}

export const convertToCurrency = data => data.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');

export const currencyFormatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'NGN',
    minimumFractionDigits: 2
});

export function TitleCase(data) {
    return `${data[0].toUpperCase()}${data.slice(1)}`;
}

export const getPaymentStatus = paymentStatus => {
    switch (paymentStatus) {
        case 'OTP_VALIDATION':
            return '/otp';

        case 'SUCCESSFUL':
            return '/success';

        // TODO: CREATE THE VBV PAGE WHICH IS AN IFRAME
        case 'VBV-VALIDATION':
            return '/vbv';

        default:
            return '/';
    }
};

export const errorMessage = code => {
    switch (code) {
        case 'P30':
            return 'Please retry the transaction again';

        default:
            return '';
    }
};
