import React from 'react';
import { Switch, Route } from 'react-router-dom';
import UnMounting from './Components/UnMounting';

import HomeRoutes from './HomeRoutes';
// const HomeRoutes = React.lazy(() => import('./HomeRoutes'));
import SuccessMessage from './Components/success-message/SuccessMessage';
// const SuccessMessage = React.lazy(() =>
//     import('./Components/success-message/SuccessMessage')
// );

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App(props) {
    // TODO: 1. if any of the required field is missing take the user back to unmounting component
    if (!!props.merchantCode === false || !!props.customer.email === false || !!props.amount === false) {
        return <UnMounting />;
    }
    return (
        <div>
            <ToastContainer style={{ fontSize: '1.6rem', fontWeight: '500' }} />
            <Switch>
                <Route path="/success" render={() => <SuccessMessage {...props} />} />
                <Route path="/" render={prop => <HomeRoutes {...prop} {...props} />} />
            </Switch>
        </div>
    );
}

export default App;
