import React, { useContext } from 'react';

import { ReactComponent as LogoMark } from '../Assets/images/svg/SPay-LogoMark.svg';
import ProcessLoader from '../Components/process-loader/process-loader';

import ErrorModal from '../Components/error-message/ErrorModal';
import { GlobalContext } from '../context/globalContext';

import Modal from '../Components/modal/modal';
import { currencyFormatter } from '../utils/TextTransform';
import CancelledModal from '../Components/CancelledModal';
import { destroy } from '../helpers/destroy';

function Checkout({ onClose, children }) {
    // since onclose function will be called here I decided to put ot here
    const {
        receiver,
        merchantCustomer: {
            customer: { email },
            amount
        },
        processLoading,
        error,
        setError,
        spayCheckoutModal,
        setSpayCheckoutModal,
        cancelled,
        setCancelled
    } = useContext(GlobalContext);

    const handleModalChange = () => {
        // TODO 2: work on the error modal to display error before closing
        // once the close button is clicked run the on error function here

        setCancelled({
            title: 'Oops You Cancelled',
            message: 'You have terminated this transaction'
        });

        if (typeof onClose === 'function') onClose();
        setTimeout(() => {
            // run onClose function here
            setSpayCheckoutModal(false);
            setTimeout(() => {
                destroy();
            }, 500);
        }, 1500);
    };

    return (
        <React.Fragment>
            <Modal
                modalVisibility={spayCheckoutModal}
                onModalChange={handleModalChange}
                styles={'oi-modal__cardwd--437'}
            >
                <div className="checkout" data-testid="checkout">
                    {/* loader in here to confirm if the process is loading or not */}
                    {processLoading && <ProcessLoader />}
                    {cancelled.title && (
                        <CancelledModal
                            title={cancelled.title}
                            message={cancelled.message}
                        />
                    )}
                    {error && (
                        <ErrorModal
                            error={error}
                            closeError={() => setError('')}
                        />
                    )}

                    <div className="merchant-logo__container">
                        <LogoMark className="merchant-logo" />
                    </div>

                    <div className="checkout__header">
                        <div className="merchant-info">
                            <div className="merchant-email">{email}</div>
                            <div className="merchant-amount">
                                <span className="merchant-amount--figure">
                                    {currencyFormatter.format(amount)}
                                </span>
                            </div>
                            <div className="merchant-receiver">
                                <span className="merchant-receiver--mod">
                                    TO:
                                </span>
                                <span className="u-half-gradient-bg">
                                    {receiver}
                                </span>
                            </div>
                        </div>
                    </div>

                    <div className="dynamic-renderer">
                        {/* the code below renders the children dynamically from the route */}
                        {children}
                    </div>
                </div>
            </Modal>
        </React.Fragment>
    );
}

export default Checkout;
