// Uncomment this line if you need to build the checkout page and embed it in another application
const hist = createBrowserHistory();

const el = document.getElementById('payWithSpay');
const paymentPortal = document.createElement('div');

paymentPortal.setAttribute('id', 'spay-integration');
el.appendChild(paymentPortal);

window.SpayCheckout = {
    init: function(props) {
        ReactDOM.render(
            <React.StrictMode>
                <BrowserRouter history={hist}>
                    <FetchProvider>
                        <GlobalDetailsProvider>
                            <App {...props} />
                        </GlobalDetailsProvider>
                    </FetchProvider>
                </BrowserRouter>
            </React.StrictMode>,

            document.getElementById('spay-integration')
        );
    }
};
