const endpoint = process.env.REACT_APP_ENDPOINT;

export const paymentUrl = () => {
    return `${endpoint}payments`;
};

export const validateOtpUrl = reference => {
    return `${endpoint}payments/${reference}/validate`;
};

export const validateVisa = reference => {
    return `${endpoint}payments/${reference}/validate`;
};

export const cardinalResponse = reference => {
    return `${endpoint}payments/cardinal/${reference}`;
};

export const getUssdBanksUrl = () => {
    return `${endpoint}payments/ussd/banks`;
};
