//   };

export const encryptDes = (object, secret) => {
    var message = JSON.stringify(object);
    var key = CryptoJS.enc.Utf8.parse(secret);

    key = CryptoJS.MD5(key);

    key.words.push(key.words[0], key.words[1]);

    var options = { mode: CryptoJS.mode.ECB };

    var textWordArray = CryptoJS.enc.Utf8.parse(message);

    var encrypted = CryptoJS.TripleDES.encrypt(textWordArray, key, options);

    var base64String = encrypted.toString();

    return base64String;
};

export const decryptDes = (encrypted, secret) => {
    var decrypted = CryptoJS.TripleDES.decrypt(encrypted, secret);
    return JSON.parse(decrypted.toString(CryptoJS.enc.Utf8));
};

// for card validation
function clearNumber(value = '') {
    return value.replace(/\D+/g, '');
}

let issuer = '';

export function formatCreditCardNumber(value) {
    if (!value) {
        return value;
    }

    issuer = Payment.fns.cardType(value);

    const clearValue = clearNumber(value);
    let nextValue;

    switch (issuer) {
        case 'amex':
            nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(4, 10)} ${clearValue.slice(10, 15)}`;
            break;
        case 'dinersclub':
            nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(4, 10)} ${clearValue.slice(10, 14)}`;
            break;
        default:
            nextValue = `${clearValue.slice(0, 4)} ${clearValue.slice(4, 8)} ${clearValue.slice(
                8,
                12
            )} ${clearValue.slice(12, 19)}`;
            break;
    }

    return nextValue.trim();
}

export function formatCVC(value, prevValue, allValues = {}) {
    const clearValue = clearNumber(value);
    let maxLength = 4;

    if (allValues.number) {
        const issuer = Payment.fns.cardType(allValues.number);
        maxLength = issuer === 'amex' ? 4 : 3;
    }

    return clearValue.slice(0, maxLength);
}
// export issuer
export const getIssuer = () => {
    return `${issuer}`;
};

export function formatExpirationDate(value) {
    const clearValue = clearNumber(value);

    if (clearValue.length >= 3) {
        return `${clearValue.slice(0, 2)}/${clearValue.slice(2, 4)}`;
    }

    return clearValue;
}

export function formatFormData(data) {
    return Object.keys(data).map(d => `${d}: ${data[d]}`);
}

export function generateRef() {
    return 'spay-test-' + Math.floor(Math.random() * 1000000000 + 1);
}
