import React, { useEffect, useContext, useState } from 'react';
import FormInput from '../form-input/form-input';
import { useHistory } from 'react-router-dom';

import { ReactComponent as MasterCard } from '../../Assets/images/svg/mc_symbol.svg';
import { ReactComponent as VisaCard } from '../../Assets/images/svg/visa-icon.svg';
import { ReactComponent as CardIconBlack } from '../../Assets/images/svg/cardIcon-black.svg';

import { formatExpirationDate, formatCreditCardNumber, formatCVC } from '../../helpers/utils';
import CheckoutNav from '../checkout-nav/checkout-nav';
import { GlobalContext } from '../../context/globalContext';
import Payment from 'payment';

function CardChannel({ others }) {
    const { setCardDetails, setMerchantCustomer, setSpayCheckoutModal } = useContext(GlobalContext);

    useEffect(() => {
        setMerchantCustomer({ ...others });
        setSpayCheckoutModal(true);
    }, []);

    const history = useHistory();

    // set card details here

    const [state, setState] = useState({
        cardNumber: '',
        expiry: '',
        cvv: '',
        issuer: ''
    });

    const [issuer, setIssuer] = useState('');

    const handleSubmit = async event => {
        event.preventDefault();

        const { cardNumber, expiry, cvv } = state;

        if (cardNumber.length < 19 || expiry.length < 3 || cvv.length < 3) {
            return;
        }
        const pan = cardNumber.split(' ').join('');

        const formattedExp = expiry.split('/').join('');

        const values = { pan, expiry: formattedExp, cvv };

        setCardDetails(values);

        if (issuer === 'visa') {
            history.push('/cardchannel/visa');
        } else {
            history.push('/cardchannel/pin');
        }
    };

    const handleInputChange = ({ target }) => {
        if (target.name === 'cardNumber') {
            // Set the card issuer based on user input
            // setIssuer(getIssuer());
            const cardType = Payment.fns.cardType(target.value);
            setIssuer(cardType);

            target.value = formatCreditCardNumber(target.value);
        }
        if (target.name === 'expiry') {
            target.value = formatExpirationDate(target.value);
        }
        if (target.name === 'cvv') {
            target.value = formatCVC(target.value);
        }

        setState({ ...state, [target.name]: target.value });
    };

    const { expiry, cardNumber } = state;

    return (
        <React.Fragment>
            <div className="checkout__nav">
                <CheckoutNav />
            </div>

            <form className="oi-form" onSubmit={handleSubmit}>
                {/* this process is the first process in card payment */}
                <div className="oi-form__group u-box-align-items u-margin-top46">
                    <div className="oi-form__group-60-of-100 oi-form__group-60-of-100--mod">
                        <FormInput
                            name="cardNumber"
                            type="tel"
                            pattern="[\d| ]{18,22}"
                            maxLength={22}
                            handleChange={handleInputChange}
                            hook="card-number"
                            label="Card Number"
                            // handleChange={this.handleChange}
                            placeholder="1234 5678 0000 9876"
                            value={state.cardNumber}
                            required
                        />
                    </div>

                    <div className="oi-form__group-40-of-100 oi-form__group-40-of-100--mod">
                        <div className="visual-card card-provider-validation">
                            <div
                                className={
                                    state.cardNumber.length >= 16 ? 'visual-card__pin--mod' : 'visual-card__pin'
                                }>
                                {(cardNumber && cardNumber) || '**** **** **** ****'}
                            </div>
                            <div className="visual-card__type">
                                <div className="visual-card__date">
                                    {/* render date if date is not empty if it is render the string */}
                                    {(expiry && expiry) || '08 / 21'}
                                </div>
                                <div className="visual-card__provider-wrap">
                                    {/* if issuer returns any of the card, display the card icon, otherwise display empty card */}
                                    {/* issuer is returning 'null' so I had to chain 'null'*/}
                                    {((!issuer || issuer === 'null') && (
                                        <CardIconBlack className="visual-card__provider" />
                                    )) ||
                                        (issuer === 'mastercard' && <MasterCard className="visual-card__provider" />) ||
                                        (issuer === 'visa' && <VisaCard className="visual-card__provider" />)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="oi-form__group u-margin-top25">
                    <div className="oi-form__group-1-of-2">
                        <FormInput
                            name="expiry"
                            type="tel"
                            hook="expiry"
                            label="Expires"
                            handleChange={handleInputChange}
                            pattern="\d\d/\d\d"
                            placeholder="08 / 21"
                            required
                        />
                    </div>
                    <div className="u-vertical-line-primary" />
                    <div className="oi-form__group-1-of-2">
                        <FormInput
                            name="cvv"
                            type="password"
                            hook="cvv"
                            label="CVV"
                            handleChange={handleInputChange}
                            placeholder="***"
                            pattern="\d{3,4}"
                            maxLength={3}
                            required
                        />
                    </div>
                </div>

                <button className="oi-form__pay-btn" type="submit">
                    Pay Now
                </button>
            </form>
        </React.Fragment>
    );
}
export default CardChannel;
