import React, { useEffect, useContext } from 'react';
import { GlobalContext } from '../context/globalContext';
import { destroy } from '../helpers/destroy';

import CancelledModal from './CancelledModal';

function UnMounting() {
    const { setSpayCheckoutModal, setCancelled, cancelled } = useContext(
        GlobalContext
    );
    useEffect(() => {
        setCancelled({
            title: 'Dev Error',
            message:
                'Please make sure all the required fields are set before proceeding'
        });

        setTimeout(() => {
            setSpayCheckoutModal(false);
            setTimeout(() => {
                destroy();
            }, 700);
        }, 2000);
    }, []);
    return (
        // if the required field is missing display the message below and close the page
        <div>
            <CancelledModal
                title={cancelled.title}
                message={cancelled.message}
            />
        </div>
    );
}

export default UnMounting;
